import React from "react";
import s from './StepFour.module.css';
import AppView from "./AppView/AppView";
import unnecessaryUrl from '../../assets/img/Step4_unnecessary_files.jpg';
import editUrl from '../../assets/img/Step4_editor.jpg';
import FileAppCss from "./FileAppCss/FileAppCss";
import FileAppJs from "./FileAppJs/FileAppJs";
import FileIndexJs from "./FileIndexJs/FileIndexJs";

const StepFour = () => {
    return (
        <>
            <p>  index.html  style.css  .     </p>

            <p> : <a href='https://ru.react.js.org/docs/create-a-new-react-app.html' target="_blank" rel="noreferrer">https://ru.react.js.org/docs/create-a-new-react-app.html</a>    </p>
            <div className={s.gatsbyHighlight}>
                <pre>
                    <code className={s.gatsbyCode}>
                        <div className={s.gatsbyText}>
                            npx create-react-app my-app
                        </div>
                        <div className={s.gatsbyText}>
                            <span className={s.token}>cd</span> my-app
                        </div>
                        <div className={s.gatsbyText}>
                            <span className={s.tokenNpm}>npm</span> start
                        </div>
                    </code>
                </pre>
            </div>
            <br/>
            <AppView/>
            <p>     React:</p>
            <div className={s.containerImg}>
                <img className={s.img_wrapper} alt=" " src={unnecessaryUrl}/>
                <span className={s.text}>   : </span>
                <img className={s.img_wrapper} alt="" src={editUrl}/>
            </div>
            <p> </p>
            <FileAppCss/>
            <FileAppJs/>
            <FileIndexJs/>
        </>
    )
}

export default StepFour;